.teachers{
  background-color: #FFFFFF;
  padding: 40px 0;
  @media screen and (min-width: 992px){
    padding: 80px 0;
  }
  .container{
    max-width: 1000px;
    position: relative;
  }
  .heading-img{
    display: none;
    @media screen and (min-width: 992px){
      display: block;
      position: absolute;
      max-width: 280px;
      left: 90px;
      top: -260px;
    }
    @media screen and (min-width: 1200px){
      left: -60px;
      top: -490px;
    }
  }
  .section-header{
    .heading{
      font-weight: 900;
      font-size: 24px;
      padding-bottom: 40px;
      color: #000000;
      @media screen and (min-width: 768px){
        font-size: 30px;
      }
      @media screen and (min-width: 992px){
        font-size: 40px;
      }
      @media screen and (min-width: 1200px){
        font-size: 50px;
      }
    }
  }
  .img-container{
    position: relative;
    img{
      width: 100%;
      display: block;
      margin: 0 auto;
    }
    .heading{
      font-weight: 900;
      position: absolute;
      left: 20px;
      bottom: 20px;
      font-size: 40px;
      line-height: 44px;
    }
  }
  .text{
    color: #000000;
    padding-top: 20px;
    text-align: center;
    br{
      @media screen and (max-width: 500px){
        display: none;
      }
      @media screen and (min-width: 768px) and (max-width: 991px){
        display: none;
      }
    }
    @media screen and (min-width: 768px){
      text-align: left;
    }
  }
}